@import "../../styles/common";

.royalty-info {
  font-size: 0.7rem;
  font-family: $neue-helvetica-medium;
}
.fit-to-height {
  min-height: calc(100vh - 5rem);
  padding-top: 2.1rem;
  overflow: hidden;
  padding-bottom: 2.1rem;
  @media screen and (min-height: 1024px) {
    min-height: 50rem !important;
  }
}
.creator {
  font-size: 1.2rem;
  font-family: $neue-helvetica-medium;
  margin-top: 0em !important;

  .check-icon {
    margin-top: -7px;
    height: 1rem !important;
    width: 1rem !important;
  }

  .nft-status-tag {
    margin-left: 1rem;
    font-size: 0.8rem;
    background-color: #f21e00;
    color: #ffffff;
    white-space: nowrap;
    height: 1.5rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 0.5rem;
    .check-icon {
      margin-top: 0 !important;
      height: 0.9rem !important;
      width: 0.9rem !important;
    }
  }
}

.read-more-button {
  cursor: pointer;
  color: #004fff;
}

.bottom-content {
  position: sticky;
  top: 100vh;
  @media screen and (max-width: 425px) {
    .current-bid {
      padding: 1rem 0;
      border-bottom: 2px solid #000;
    }
    hr.custom-divider {
      display: none;
    }
  }
  .d-flex {
    @media screen and (max-width: 425px) {
      flex-direction: column;
    }
  }
}

.place-bid-btn {
  font-family: $neue-helvetica-medium;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
  width: 80%;
  border: 2px solid #000000;

  &:disabled {
    background-color: #e7e7e9;
    color: #9c9c9b;
    border: none;
  }

  &:hover {
    background-color: #ffffff;
    color: #000000;
  }
}

.place-bid-buy-btn {
  border-radius: 50rem;
  border: solid 1px #000;
  background-color: #000;
  color: #fff;
  display: block;
  margin: 0 auto 1rem;
  padding: 0.6rem 2rem;
  transition: 0.3s;
  position: relative;
  font-family: $neue-helvetica-extended-bold;
  text-transform: uppercase;
  font-size: 1rem;
  &::before {
    content: "";
    position: absolute;
    top: -0.25rem;
    right: -0.25rem;
    bottom: -0.25rem;
    left: -0.25rem;
    background: transparent
      linear-gradient(180deg, #e2ff00 0%, #18e0e0 47%, #e8318d 100%) 0% 0%
      no-repeat padding-box;
    border-radius: 50rem;
    z-index: -1;
  }
  &:hover,
  &:focus,
  &:active {
    border: solid 1px #000;
    background: #fff;
    color: #000;
  }
  &:disabled {
    color: #9c9c9b;
    border: none;
    opacity: 1;
    &::before {
      opacity: 0.5;
    }
  }
}

.recharge-btn {
  font-family: $neue-helvetica-medium;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
  width: 80%;

  &:hover {
    background-color: #ffffff;
    color: rgb(236, 41, 41) !important;
  }
}

.nft-title-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  .nft-title {
    font-size: 2.4rem;
    font-family: $neue-helvetica-bold;
    line-height: 1.3;
  }

  .discord {
    border: 1px solid #e7e7e9;
    border-radius: 0.3rem;
    padding: 0.5rem;
    position: relative;
    cursor: pointer;

    .count {
      position: absolute;
      right: -0.3rem;
      top: -0.3rem;
      background-color: #fafafa;
      border: 1px solid #e7e7e9;
      color: #9b9b9b;
      border-radius: 50%;
      width: 1.8rem;
      height: 1.8rem;
      font-size: 0.8rem;
      font-family: "neue_helveticabold" !important;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 500;
    }

    svg {
      height: 2rem;
      width: 2rem;

      path {
        fill: #000000;
      }
    }
  }
}

.nft-desc {
  font-size: 1.1rem;
  font-family: $neue-helvetica-medium;
}

.custom-divider {
  height: 2px !important;
  background-color: #000000;
  opacity: 1;
  margin: 0.5rem 0 1.5rem 0;
}

@media screen and (max-width: 991px) {
  .fit-to-height {
    height: auto;
  }
  .creator,
  .creator.mt-3 {
    margin-top: 0rem !important;
  }
}

.cancel-nft-list {
  padding: 0;
  margin: 0;
  list-style: none;
  .cancel-nft-item {
    margin-bottom: 1rem;
    article.cancel-nft {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      width: 100%;
      .image-block {
        width: 5rem;
        height: 5rem;
        img {
          height: 5rem;
          width: 5rem;
          object-fit: cover;
          object-position: center;
        }
      }
      .info-block {
        width: calc(100% - 5rem);
        padding-left: 0.5rem;
        .name {
          font-family: $neue-helvetica-medium;
        }
        .nft-info {
          padding: 0;
          margin: 0;
          li {
            display: flex;
            .key {
              width: 70px;
              font-size: 0.95rem;
              font-family: $neue-helvetica-light;
            }
            .value {
              font-family: $neue-helvetica-medium;
            }
          }
        }
        .cancel-btn {
          &:hover,
          &:focus,
          &:active {
            background-color: #fff;
            color: #000;
          }
        }
      }
    }
  }
}

.scroll-modal {
  padding: 0.5rem;
  max-height: 70vh;
}

.assign-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  background: #fff;
  margin: 0 0 2rem;
  @media screen and (max-width: 425px) {
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
  }

  .first-half {
    display: flex;
    align-items: center;
    width: calc(100% - 10rem);
    @media screen and (max-width: 425px) {
      width: calc(100%);
    }

    img {
      height: 3rem;
      width: 3rem;
      object-fit: cover;
      border-radius: 50%;
      margin-right: 1rem;
      background-color: #ccc;
    }

    .time {
      font-size: 1rem;
      font-family: $neue-helvetica-medium;
      .expire-pill {
        padding: 0.3rem 0.5rem;
        background-color: #bdbdbc;
        color: #fff;
        border-radius: 0.3rem;
        margin-left: 0.5rem;
        font-size: 0.8rem;
        white-space: nowrap;
        @media screen and (max-width: 767px) {
          margin-left: 0;
        }
        &.active {
          background-color: #4caf50;
        }
      }
      @media screen and (max-width: 767px) {
        display: flex;
        flex-direction: column-reverse;
      }
    }

    .bid-owner {
      font-size: 1.15rem;
      font-family: $neue-helvetica-medium;

      .owner-id {
        cursor: pointer;

        &:hover {
          color: #000 !important;
        }
      }
    }
  }
  .second-half {
    @media screen and (max-width: 425px) {
      width: calc(100%);
    }
    .highest-bid {
      border: 2px solid #000;
      padding: 1rem 0.8rem;
      display: flex;
      flex-direction: column;
      border-radius: 0.5rem;
      margin: 1rem 0;
      @media screen and (max-width: 425px) {
        width: calc(100%);
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
      }
      .key {
        font-size: 0.8rem;
        font-family: $neue-helvetica-medium;
        margin-bottom: 0.2rem;
      }
      .value {
        font-size: 2rem;
        font-family: $neue-helvetica-medium;
        line-height: 2rem;
      }
    }
  }
}

.assign-sold {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 30vh;
}

.bredcrumb-link {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  @media screen and (max-width: 991px) {
    margin-top: 1rem;
    margin-bottom: 0.2rem;
  }
  li {
    margin-bottom: 0.2rem;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    span {
      font-size: 1rem;
      font-family: $neue-helvetica-medium;
      color: #fff;
      // padding-right: 1rem;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      text-decoration: none;
      cursor: pointer;
      &:hover,
      &:focus,
      &:active {
        text-decoration: underline;
      }
      .icon {
        font-size: 1.4rem;
        margin: 0 0.2rem;
      }
    }
    &:last-child {
      span {
        .icon {
          visibility: hidden;
        }
      }
    }
  }
}

.bid-expire-alert {
  font-family: $neue-helvetica-light;
  font-size: 1rem;
  color: red;
  text-align: center;
  padding: 0.5rem 1rem;
  background: white;
  display: inline-flex;
  border-radius: 50rem;
  border: 0.2rem solid red;
  align-items: center;
  .nft-counter {
    margin-left: 0.4rem;
    .counter-time {
      font-size: 1rem;
      font-family: $neue-helvetica-bold;
    }
    .counter-interval {
      margin-left: 0.2rem;
      font-size: 0.9rem;
      font-family: $neue-helvetica-bold;
    }
    .interval-gap {
      margin-right: 0.3rem;
    }
  }
}

.flex-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  button {
    width: 48%;
    margin: 1%;
    margin-bottom: 1rem;
    &.add-to-cart-btn {
      background: #093321;
      color: #fff;
      text-transform: uppercase;
      border: 0.1rem solid #113b29;
      padding: 0.8rem;
      border-radius: 50rem;
      font-family: $neue-helvetica-bold;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        height: 1.2rem;
        filter: invert(0);
        margin-right: 0.5rem;
      }
      &.added-to-cart {
        background: #e1ff04;
        color: #000;
        img {
          filter: invert(1);
        }
      }
    }
    &.full-width {
      width: 98%;
    }

    @media screen and (max-width: 480px) {
      width: 98%;
    }
  }
}
